:root {
  --icon-border: #b0bec5;
  --icon-border-active: #607D8B;
  --icon: #1a237e
}

.circle {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;

  -webkit-transition: -webkit-transform .2s;

  transition: -webkit-transform .2s;

  -o-transition: transform .2s;

  transition: transform .2s;

  transition: transform .2s, -webkit-transform .2s;
}
.circle:not(.active) {
  cursor: pointer;
}
.circle:not(.active):hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.icon-inside {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 43px;
  height: 43px;
  border: 2px solid var(--icon-border);
}
.icon-inside.active {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  border: 2px solid var(--icon-border-active);
}

.icon-inside > .icon {
  width: 60%;
  height: 60%;
  color: var(--icon);
}



